<template>
  <div class="aaa">
    <div class="linear" ref='linear' style='width: 400px;height: 400px;'>

    </div>
    <img src="../../assets/imgs/holeindex/chengse.png" alt="">
  </div>
</template>

<script>
  export default{
    mounted(){
      this.init();
    },
    methods: {
      init(){
        let myChart = this.$echarts.init(this.$refs.linear);
        var getname = ['小说', '教材', '期刊', '其他','484','878','tyjty','rthrt','rthrt','rgt'];
        var getvalue = [10, 10, 10, 10,10, 10,10, 10,10, 10];
        let datanum = [15,21,54,54,545,556,45,5,1,5];
        var data = [];
        for (var i = 0; i < getname.length; i++) {
            data.push({
                name: getname[i],
                value: getvalue[i],
                a: datanum[i],
                num: (i+1)+'级'
            })
        }
        var colorList = ['#0081de','#0081de','#0081de','#0081de','#0081de','#0081de','#0081de','#0081de','#0081de','#0081de'];
        var rich = {
            name: {
                color: "#FFF",
                fontSize: 14,
                padding: [10, 15, 0, 10],
                fontWeight: '400',
                align: 'left'
            },
            value: {
                color: "#FFF",
                fontSize: 15,
                padding: [10, 10, 0, 15],
                fontWeight: '500',
                align: 'right'
            },
            percent: {
                color: "#FFF",
                align: 'right',
                fontSize: 15,
                fontWeight: '500',
                //padding: [0, 5]
            },
            hr: {
                width: '100%',
                height: 0,
            },
            cir: {
                fontSize: 26,
            }
        }
        //职称结构图表

        let option = {
            backgroundColor: 'transparent',
            tooltip: {
                trigger: 'axis',
            },
            series: [{
                tooltip: {
                    trigger: 'item',
                    formatter: function(params) {
                        return params.data.num + '：' + params.data.a + '个';
                    }
                },
                itemStyle: {
                    normal: {
                        borderColor: '#0A1934',
                        borderWidth: 5,
                        color: function(params) {
                            return params.data.a? colorList[params.dataIndex]:'#01315a';
                        }
                    }
                },
                type: 'pie',
                radius: ['30%', '50%'],
                center: ["50%", "50%"],
                label: {
                    normal: {
                        show: false,
                        position: 'inner',
                        formatter: params => {
                            return (
                                '{percent|' + params.percent.toFixed(0) + '%}'
                            );
                        },
                        rich: rich,
                    }
                },
                data: data
            }, {
                itemStyle: {
                    normal: {
                        borderColor: '#0A1934',
                        borderWidth: 5,
                        color: function(params) {
                            return colorList[params.dataIndex];

                        }
                    }
                },
                type: 'pie',
                silent: true, //取消高亮
                radius: ['30%', '50%'],
                center: ["50%", "50%"],
                labelLine: {
                    normal: {
                        length: 30,
                        length2: 0,
                        lineStyle: {
                            color: 'transparent'
                        }
                    }
                },
                label: {
                    normal: {

                        formatter: params => {
                            return params.data.a?'{value|' + (params.data.a) +'个' +'}\n{hr|—————}':'';
                        },
                        rich: rich,
                        padding: [-20, 25, 0, 25]
                    }
                },
                data: data,
                z: -1
            }, {
                itemStyle: {
                    normal: {
                        borderColor: '#0A1934',
                        borderWidth: 5,
                        color: function(params) {
                            return params.data.a ?colorList[params.dataIndex]: 'transparent'
                        }
                    }
                },
                type: 'pie',
                silent: true, //取消高亮
                radius: ['30%', '50%'],
                center: ["50%", "50%"],
                labelLine: {
                    normal: {
                        length: 30,
                        length2: 0,
                        lineStyle: {
                            color: 'transparent'
                        }
                    }
                },
                label: {
                    normal: {
                        formatter: params => {
                            return '\n{cir|●}\n';
                        },
                        rich: rich,
                    }
                },
                data: data,
                z: -1
            }]
        };
        myChart.setOption(option);

      }
    }
  }
</script>

<style scoped lang='less'>
  .aaa{
    position: relative;
    top: -25px;
    img{
      width: 110px;
      height: 110px;
      position: absolute;
      top: 145px;
      left: 145px;
    }
  }
</style>
