<template>
  <div class="linear" ref='linear' style='width: 400px;height: 300px;'>

  </div>
</template>

<script>
  export default{
    mounted(){
      this.init();
    },
    methods: {
      init(){
        let myChart = this.$echarts.init(this.$refs.linear);
        var data = {

                area: ['周一','周二','周三','周四','周五','周六','周日'],
                legend: ['一级','二级','三级'],
                data: [
                    [50,50,50,50,50,50,50],
                    [50,50,50,50,50,50,50],
                    [150,140,130,160,50,40,20],
                ]


        }
        var colors = ['#74F7FE','#73AEEE','#488AC7']
        var option = {
            title: {
                left: 19,
                top: 15,
                textStyle: {
                    color: '#fff'
                }
            },
            color: colors,
            legend: {
                top: 10,
                left: 200,
                itemWidth: 10,
                itemHeight: 10,
                // padding: [5, 10],
                textStyle: {
                    fontSize: 14,
                    color: '#96A4F4',
                    padding: [3, 0, 0, 0]
                },
                data: data.legend
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '13%',
                containLabel: true
            },
            xAxis: {
                type: 'category',
                axisLabel: {
                    color: '#96A4F4'
                },
                axisLine: {
                    lineStyle: {
                        color: '#96A4F4'
                    },
                    width: 5
                },
                axisTick: {
                    show: false,
                },
                data: data.area
            },
            yAxis: {
                type: 'value',
                axisLabel: {
                    color: '#96A4F4'
                },
                axisLine: {
                    lineStyle: {
                        color: '#96A4F4'
                    },
                    width: 5
                },
                axisTick: {
                    show: false,
                },
                splitLine: {
                    lineStyle: {
                        color: 'rgba(150, 164, 244, 0.3)'
                    }
                },
            },
            series: [

            ]
        };
        for (var i = 0; i < data.legend.length; i++) {
            option.series.push({
                name: data.legend[i],
                type: 'bar',
                stack: '总量',
                barWidth: '45%',
                label: {
                    show: false,
                    position: 'insideRight'
                },
                data: data.data[i]
            })
        }
        myChart.setOption(option);

      }
    }
  }
</script>

<style scoped>
  .linear{
    margin-left: 40px;
  }
</style>
